import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {HttpModule} from "@angular/http";
import {FormsModule} from "@angular/forms";
import { Router, RouterModule, Routes } from '@angular/router';

import {MatButtonModule, MatCardModule, MatProgressBarModule, MatTabsModule, MatFormFieldModule, MatInputModule, MatSnackBarModule, MatDialogModule, MatAutocompleteModule, MatIconModule} from '@angular/material';

import { AppComponent } from './pd--app/app.component';
import { PdCatalogPageComponent } from './pd--catalog-page/pd--catalog-page.component';
import { PdCollectionsPageComponent } from './pd--collections-page/pd--collections-page.component';
import { PdBasePageComponent } from './pd--base-page/pd-base-page.component';
import { PdHeaderComponent } from './pd--header/pd--header.component';
import { PdFooterComponent } from './pd--footer/pd--footer.component';
import { PdStylesComponent } from './pd--styles/pd--styles.component';
import { PdCollectionComponent } from './pd--collection/pd--collection.component';
import { PdProductPageComponent } from './pd--product-page/pd--product-page.component';
import {ImageScrollerComponent} from "./hk--image-scroller/hk--image-scroller.component";
import { PdOfficeLoginComponent } from './pd--office-login/pd--office-login.component';
import { PdOfficeStylesComponent } from './pd--office-styles/pd--office-styles.component';
import {PdAuthGuard} from "./pd--auth.guard";
import {PdAuthService} from "./pd--auth.service";
import { PdBucketComponent } from './pd--bucket/pd--bucket.component';
import {PdBucketService} from "./pd--bucket.service";
import { PdRegisterPageComponent } from './pd--register-page/pd--register-page.component';
import { PdOfficeBasePageComponent } from './pd--office-base-page/pd--office-base-page.component';
import { PdDialogComponent } from './pd--dialog/pd--dialog.component';
import { PdOfficeTypesComponent } from './pd--office-types/pd--office-types.component';
import { PdOfficeCollectionsComponent } from './pd--office-collections/pd--office-collections.component';
import { PdCollectionsPipe } from './pd--collections.pipe';
import {StylesService} from "./pd--styles.service";
import {CollectionsService} from "./pd--collections.service";
import { StylesPipe } from './pd--styles.pipe';
import { PdAutocompleteComponent } from './pd--autocomplete/pd--autocomplete.component';
import {PdTypesService} from "./pd--types.service";
import { PdOfficeProductsComponent } from './pd--office-products/pd--office-products.component';
import {PdProductsService} from "./pd--products.service";
import { PdRemnantsReportComponent } from './pd--remnants-report/pd--remnants-report.component';


const appRoutes: Routes = [
  { path: 'catalog', component: PdCatalogPageComponent },
  { path: 'collections', component: PdCollectionsPageComponent },
  { path: 'collections/:collection', component: PdCollectionComponent },
  { path: 'collections/:collection/:idProduct', component: PdProductPageComponent },
  { path: 'bucket', component: PdBucketComponent },
  { path: 'styles', component: PdStylesComponent },
  { path: 'shares', component: PdCatalogPageComponent },

  { path: 'register', component: PdRegisterPageComponent },

  { path: 'login', component: PdOfficeLoginComponent },
  { path: 'office/products', component: PdOfficeProductsComponent, canActivate: [PdAuthGuard] },
  { path: 'office/collections', component: PdOfficeCollectionsComponent, canActivate: [PdAuthGuard] },
  { path: 'office/styles', component: PdOfficeStylesComponent, canActivate: [PdAuthGuard]  },
  { path: 'office/types', component: PdOfficeTypesComponent, canActivate: [PdAuthGuard] },

  { path: 'office/reports/remnants', component: PdRemnantsReportComponent, canActivate: [PdAuthGuard] },

  { path: '', redirectTo: '/catalog', pathMatch: 'full' },
  { path: '**', redirectTo: '/catalog' }
];

@NgModule({
  declarations: [
    AppComponent,
    PdCatalogPageComponent,
    PdCollectionsPageComponent,
    PdBasePageComponent,
    PdHeaderComponent,
    PdFooterComponent,
    PdStylesComponent,
    PdCollectionComponent,
    PdProductPageComponent,
    ImageScrollerComponent,
    PdOfficeLoginComponent,
    PdOfficeStylesComponent,
    PdBucketComponent,
    PdRegisterPageComponent,
    PdOfficeBasePageComponent,
    PdDialogComponent,
    PdOfficeTypesComponent,
    PdOfficeCollectionsComponent,
    PdCollectionsPipe,
    StylesPipe,
    PdAutocompleteComponent,
    PdOfficeProductsComponent,
    PdRemnantsReportComponent
  ],
  imports: [
    RouterModule.forRoot(appRoutes),
    BrowserModule,
    FormsModule,
    HttpModule,
    BrowserAnimationsModule,
    MatTabsModule,
    MatProgressBarModule,
    MatCardModule,
    MatButtonModule,
    MatFormFieldModule,
    MatInputModule,
    MatSnackBarModule,
    MatDialogModule,
    MatAutocompleteModule,
    MatIconModule
  ],
  providers: [PdAuthGuard, PdAuthService, PdBucketService, StylesService, CollectionsService, StylesPipe, PdTypesService, PdProductsService],
  bootstrap: [AppComponent],
  entryComponents: [
    PdDialogComponent
  ]
})
export class AppModule { }



