import { Injectable } from '@angular/core';
import {Http} from "@angular/http";
import {environment} from "../environments/environment";

@Injectable()
export class PdTypesService {

  private _base = environment["pd--api-url"];

  types: any[] = [];

  constructor(private http: Http) { }

  getTypes(){
    return this.http.get(this._base + 'office/types.php').map(res => this.types = res.json());
  }

  addType(name: string, description: string){
    return this.http.post(this._base + 'office/types.php', JSON.stringify({name: name, description: description})).map(res => this.types = res.json());
  }

  updateType(idType: number, name: string, description: string){
    return this.http.put(this._base + 'office/types.php', JSON.stringify({idType: idType, name: name, description: description})).map(res => this.types = res.json());
  }

  deleteType(id: number){
    return this.http.delete(this._base + 'office/types.php/' + id).map(res => this.types = res.json());
  }

}



