import {Component, OnInit} from '@angular/core';
import {environment} from "../../environments/environment";
import {Http} from "@angular/http";
import {ActivatedRoute} from "@angular/router";
import {PdBucketService} from "../pd--bucket.service";
import {MatSnackBar} from "@angular/material";

@Component({
  selector: 'app-pd--product-page',
  templateUrl: './pd--product-page.component.html',
  styleUrls: ['./pd--product-page.component.scss']
})
export class PdProductPageComponent implements OnInit {

  private _base = environment["pd--api-url"];

  product: any;
  isReady: boolean = false;

  constructor(private http: Http, private activateRoute: ActivatedRoute, private bucket: PdBucketService, public snackBar: MatSnackBar) {
    this.http.get(this._base + "collections/product.php/" + activateRoute.snapshot.params['collection'] + "/" + activateRoute.snapshot.params['idProduct']).subscribe((res) => {
      this.product = res.json();
      this.product.images = this.product.images.map(i => this._base + "images/" + i.name);
      this.isReady = true;
    });
  }

  addInBucket() {
    this.bucket.addProduct(this.product);
    this.snackBar.open("Товар успешно добавлен в корзину", "Успех", {
      duration: 2000
    });
  }

  ngOnInit() {
  }

}



