import { Component, OnInit } from '@angular/core';
import {MatDialog, MatDialogConfig} from "@angular/material";
import {CollectionsService} from "../pd--collections.service";
import {PdDialogComponent} from "../pd--dialog/pd--dialog.component";
import {PdTypesService} from "../pd--types.service";

@Component({
  selector: 'app-pd--office-types',
  templateUrl: './pd--office-types.component.html',
  styleUrls: ['./pd--office-types.component.scss']
})
export class PdOfficeTypesComponent implements OnInit {

  types: any[];

  matDialogConfig: MatDialogConfig = new MatDialogConfig();

  constructor(private typesService: PdTypesService, public dialog: MatDialog) {
    this.typesService.getTypes().subscribe(res => this.types = res);
  }

  ngOnInit() {
  }

  onUpdate(type) {
    let fields = [
      {name: 'Название', type: 'text', value: type.name, required: true},
      {name: 'Описание', type: 'textarea', value: type.description, required: false}
    ];
    this.matDialogConfig.data = {
      title: `Изменить тип ${type.name}`,
      onSubmit: () => {
        this.typesService.updateType(type.idType, fields[0].value, fields[1].value).subscribe(res => this.types = res);
      },
      onDelete: () => {
        this.typesService.deleteType(type.idType).subscribe(res => this.types = res);
      },
      fields: fields
    };
    let dialogRef = this.dialog.open(PdDialogComponent, this.matDialogConfig);
    dialogRef.afterClosed().subscribe();
  }

  onAdd() {
    let fields = [
      {name: 'Название', type: 'text', value: null, required: true},
      {name: 'Описание', type: 'text', value: null, required: false}
    ];
    this.matDialogConfig.data = {
      title: `Добавить тип товара`,
      onSubmit: () => {
        this.typesService.addType(fields[0].value, fields[1].value).subscribe(res => this.types = res);
      },
      fields: fields
    };
    let dialogRef = this.dialog.open(PdDialogComponent, this.matDialogConfig);
    dialogRef.afterClosed().subscribe();
  }

}



