import { Component, OnInit } from '@angular/core';
import {MatDialog, MatDialogConfig} from "@angular/material";
import {PdDialogComponent} from "../pd--dialog/pd--dialog.component";
import {CollectionsService} from "../pd--collections.service";
import {StylesService} from "../pd--styles.service";
import {DropdownValue} from "../pd--autocomplete/pd--autocomplete.component";

@Component({
  selector: 'app-pd--office-collections',
  templateUrl: './pd--office-collections.component.html',
  styleUrls: ['./pd--office-collections.component.scss']
})
export class PdOfficeCollectionsComponent implements OnInit {

  collections: any[];

  stylesDV: DropdownValue[];

  matDialogConfig: MatDialogConfig = new MatDialogConfig();

  constructor(private collectionsService: CollectionsService, private stylesService: StylesService, public dialog: MatDialog) {
    this.collectionsService.getCollections().subscribe(res => this.collections = res);
    this.stylesService.getStyles().subscribe(res => this.stylesDV = res.map(i => new DropdownValue(i.idStyle, i.name)));
  }

  ngOnInit() {
  }

  onUpdate(collection) {
    let fields = [
      {name: 'Название', type: 'text', value: collection.name, required: true},
      {name: 'Стиль', type: 'auto', value: this.stylesDV.find(i => i.id === collection.idStyle), required: true, items: this.stylesDV}
    ];
    this.matDialogConfig.data = {
      title: `Изменить коллекцию ${collection.name}`,
      onSubmit: () => {
        this.collectionsService.updateCollection(collection.idCollection, fields[0].value, fields[1].value.id).subscribe(res => this.collections = res);
      },
      onDelete: () => {
        this.collectionsService.deleteCollection(collection.idCollection).subscribe(res => this.collections = res);
      },
      fields: fields
    };
    let dialogRef = this.dialog.open(PdDialogComponent, this.matDialogConfig);
    dialogRef.afterClosed().subscribe();
  }

  onAdd() {
    let fields = [
      {name: 'Название', type: 'text', value: null, required: true},
      {name: 'Стиль', type: 'auto', value: null, required: true, items: this.stylesDV}
    ];
    this.matDialogConfig.data = {
      title: `Добавить коллекцию`,
      onSubmit: () => {
        this.collectionsService.addCollection(fields[0].value, fields[1].value.id).subscribe(res => this.collections = res);
      },
      fields: fields
    };
    let dialogRef = this.dialog.open(PdDialogComponent, this.matDialogConfig);
    dialogRef.afterClosed().subscribe();
  }

}



