import {Component, OnInit} from '@angular/core';
import {MatDialog, MatDialogConfig} from "@angular/material";
import {DropdownValue} from "../pd--autocomplete/pd--autocomplete.component";
import {CollectionsService} from "../pd--collections.service";
import {PdDialogComponent} from "../pd--dialog/pd--dialog.component";
import {PdProductsService} from "../pd--products.service";
import {PdTypesService} from "../pd--types.service";

@Component({
  selector: 'app-pd--office-products',
  templateUrl: './pd--office-products.component.html',
  styleUrls: ['./pd--office-products.component.scss']
})
export class PdOfficeProductsComponent implements OnInit {

  products: any[];

  collectionsDV: DropdownValue[];
  typesDV: DropdownValue[];

  matDialogConfig: MatDialogConfig = new MatDialogConfig();

  constructor(private collectionsService: CollectionsService, private typesService: PdTypesService, private productsService: PdProductsService, public dialog: MatDialog) {
    this.productsService.getProducts().subscribe(res => this.products = res);
    this.collectionsService.getCollections().subscribe(res => this.collectionsDV = res.map(i => new DropdownValue(i.idCollection, i.name)));
    this.typesService.getTypes().subscribe(res => this.typesDV = res.map(i => new DropdownValue(i.idType, i.name)));
  }

  ngOnInit() {
  }

  onUpdate(product) {
    let fields = [
      {name: 'Тип', type: 'auto', value: this.typesDV.find(i => i.id === product.idType), items: this.typesDV, required: true},
      {name: 'Коллекция', type: 'auto', value: this.collectionsDV.find(i => i.id === product.idCollection), items: this.collectionsDV, required: true},
      {name: 'Цена', type: 'number', value: product.price, required: true},
      {name: 'Название', type: 'text', value: product.name, required: false},
      {name: 'Маркировка', type: 'text', value: product.mark, required: true},
      {name: 'Длинна(мм)', type: 'number', value: product.length, required: true},
      {name: 'Ширина(мм)', type: 'number', value: product.width, required: true},
      {name: 'Высота(мм)', type: 'number', value: product.height, required: true},
      {name: 'Описание', type: 'text', value: product.description, required: false}
    ];
    this.matDialogConfig.data = {
      title: `Изменить товар ${product.name || ''} ${product.typeName} "${product.collectionName}"`,
      onSubmit: () => {
        this.productsService.updateProduct(product.idProduct, fields[0].value.id, fields[1].value.id, fields[2].value, fields[3].value, fields[4].value, fields[5].value, fields[6].value, fields[7].value, fields[8].value).subscribe(res => this.products = res);
      },
      onDelete: () => {
        this.productsService.deleteProduct(product.idProduct).subscribe(res => this.products = res);
      },
      fields: fields
    };
    let dialogRef = this.dialog.open(PdDialogComponent, this.matDialogConfig);
    dialogRef.afterClosed().subscribe();
  }

  onAdd() {
    FormData
    let fields = [
      {name: 'Тип', type: 'auto', value: null, items: this.typesDV, required: true},
      {name: 'Коллекция', type: 'auto', value: null, items: this.collectionsDV, required: true},
      {name: 'Цена', type: 'number', value: null, required: true},
      {name: 'Название', type: 'text', value: null, required: false},
      {name: 'Маркировка', type: 'text', value: null, required: true},
      {name: 'Длинна(мм)', type: 'number', value: null, required: true},
      {name: 'Ширина(мм)', type: 'number', value: null, required: true},
      {name: 'Высота(мм)', type: 'number', value: null, required: true},
      {name: 'Описание', type: 'textarea', value: null, required: false}
    ];
    this.matDialogConfig.data = {
      title: `Добавить товар`,
      onSubmit: () => {
        this.productsService.addProduct(fields[0].value.id, fields[1].value.id, fields[2].value, fields[3].value, fields[4].value, fields[5].value, fields[6].value, fields[7].value, new Date(), fields[8].value).subscribe(res => this.products = res);
      },
      fields: fields
    };
    let dialogRef = this.dialog.open(PdDialogComponent, this.matDialogConfig);
    dialogRef.afterClosed().subscribe();
  }

}



