import {Component, forwardRef, Input, OnChanges, OnInit, SimpleChanges} from '@angular/core';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from "@angular/forms";

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => PdAutocompleteComponent),
  multi: true
};

export class DropdownValue {

  private _id: any;
  private _label: string;

  public get id(): any {
    return this._id;
  }

  public get label(): string {
    return this._label;
  }

  constructor(id: any, label: string) {
    this._id = id;
    this._label = label;
  }

  public equals(oth: DropdownValue): boolean {
    if (!oth) {
      return false;
    }
    return this.id === oth.id;
  }

  public static createEmpty(): DropdownValue {
    const ddv = new DropdownValue(null, null);
    return ddv;
  }
}

@Component({
  selector: 'app-pd--autocomplete',
  templateUrl: './pd--autocomplete.component.html',
  styleUrls: ['./pd--autocomplete.component.scss'],
  providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class PdAutocompleteComponent implements OnInit, ControlValueAccessor {

  //private _values: DropdownValue[] = [];
  private _filteredValues: DropdownValue[] = [];
  private _selectedValue: DropdownValue = null;

  private _onTouchedCallback: () => void;
  private _onChangeCallback: (dropdownValue: DropdownValue) => void;

  @Input()
  values: DropdownValue[];

  /*@Input()
  public set values(v: DropdownValue[]) {
    this._values = v;
  }*/

  public get filteredValues(): DropdownValue[] {
    return this._filteredValues;
  }

  public get selectedValueLabel() {
    return this._selectedValue && this._selectedValue.label;
  }

  public get selectedValue() {
    return this._selectedValue;
  }

  @Input()
  placeholder: string;

  @Input()
  required: string;

  writeValue(obj: any): void {
    if (!this._selectedValue) {
      this._selectedValue = obj;
    } else {
      this._selectedValue = obj;
      this._onTouchedCallback();
      this._onChangeCallback(this._selectedValue);
    }
  }

  registerOnChange(fn: any): void {
    this._onChangeCallback = fn;
  }

  registerOnTouched(fn: any): void {
    this._onTouchedCallback = fn;
  }

  onChange(item) {
    this._selectedValue = item;
    this._onTouchedCallback();
    this._onChangeCallback(this._selectedValue);
  }

  onSearch(value) {
    let tmp = this.values.find(i => i.label.toLowerCase() === value.toLowerCase());
    if (tmp) {
      this._selectedValue = tmp;
      this._onTouchedCallback();
      this._onChangeCallback(this._selectedValue);
    }
    this._filteredValues = this.values.filter(i => i.label.toLowerCase().indexOf(value.toLowerCase()) !== -1);
  }

  constructor() {
  }

  ngOnInit() {
    this._filteredValues = this.values;
  }

}



