import { Injectable } from '@angular/core';
import {environment} from "../environments/environment";
import {Http} from "@angular/http";


@Injectable()
export class StylesService {

  private _base = environment["pd--api-url"];

  styles: any[] = [];

  constructor(private http: Http) { }

  getStyles(){
    return this.http.get(this._base + 'office/styles.php').map(res => this.styles = res.json());
  }

  addStyle(name: string, description: string){
    return this.http.post(this._base + 'office/styles.php', JSON.stringify({name: name, description: description})).map(res => this.styles = res.json());
  }

  updateStyle(id: number, name: string, description: string){
    return this.http.put(this._base + 'office/styles.php', JSON.stringify({idStyle: id, name: name, description: description})).map(res => this.styles = res.json());
  }

  deleteStyle(id: number){
    return this.http.delete(this._base + 'office/styles.php/' + id).map(res => this.styles = res.json());
  }

}



