import { Component, OnInit } from '@angular/core';
import {Http} from "@angular/http";
import {environment} from "../../environments/environment";
import {NgForm} from "@angular/forms";
import {PdAuthService} from "../pd--auth.service";

@Component({
  selector: 'app-pd--register-page',
  templateUrl: './pd--register-page.component.html',
  styleUrls: ['./pd--register-page.component.scss']
})
export class PdRegisterPageComponent implements OnInit {

  private _base = environment["pd--api-url"];

  constructor(private authService: PdAuthService) { }

  ngOnInit() {
  }

  onSubmit(form: NgForm){
    this.authService.register(form.value);
  }

}



