import { Component, OnInit } from '@angular/core';
import {environment} from "../../environments/environment";
import {Http} from "@angular/http";
import {ActivatedRoute} from "@angular/router";

@Component({
  selector: 'app-pd--collection',
  templateUrl: './pd--collection.component.html',
  styleUrls: ['./pd--collection.component.scss']
})
export class PdCollectionComponent implements OnInit {

  private _base = environment["pd--api-url"];

  products: any[];
  isReady: boolean = false;

  constructor(private http: Http, private activateRoute: ActivatedRoute) {
    this.http.get(this._base + "collections/collection.php/" + activateRoute.snapshot.params['collection']).subscribe((res) => {
      this.products = res.json();
      this.isReady = true;
    });
  }

  ngOnInit() {
  }

}



