import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {PdAuthService} from "../pd--auth.service";

@Component({
  selector: 'app-pd--header',
  templateUrl: './pd--header.component.html',
  styleUrls: ['./pd--header.component.scss']
})
export class PdHeaderComponent implements OnInit {

  url;
  isAdmin: boolean = false;


  constructor(activateRoute: ActivatedRoute, public authService: PdAuthService) {
    this.url = activateRoute.snapshot.url[0].path;
    this.isAdmin = authService.isAdmin;
  }

  logOut(event){
    this.authService.logOut();
    event.preventDefault();
  }

  ngOnInit() {
  }

}



