import {Injectable} from '@angular/core';
import {environment} from "../environments/environment";
import {Http} from "@angular/http";
import {Router} from "@angular/router";
import {MatSnackBar} from "@angular/material";
import {PdBucketService} from "./pd--bucket.service";

@Injectable()
export class PdAuthService {

  isLoggined: boolean = false;

  isAdmin: boolean = false;

  userInfo: any;

  private _base = environment["pd--api-url"];

  constructor(private http: Http, private router: Router, public snackBar: MatSnackBar, private bucketService: PdBucketService) {
  }

  logIn(login, password) {
    this.http.post(this._base + "auth.php", JSON.stringify({login, password})).subscribe(res => {
      this.isLoggined = true;
      this.snackBar.open("Вход выполнен успешно", "Успех", {
        duration: 2000,
      });
      if (res.json() === "Admin") {
        this.isAdmin = true;
        this.router.navigate(['/office/styles']);
      } else {
        this.userInfo = res.json();
        this.router.navigate(['/collections']);
      }
    }, error => {
      this.snackBar.open("Неверный логин или пароль", "Ошибка", {
        duration: 2000
      });
    });
  }

  register(data){
    this.http.post(this._base + 'users/add.php', JSON.stringify(data)).subscribe(res => {
      this.router.navigate(['/collections']);
      this.userInfo = data;
      this.isLoggined = true;
      this.isAdmin = false;
    }, error => {
      this.snackBar.open(error.json(), "Ошибка", {
        duration: 2000
      });
    });
  }

  logOut() {
    this.userInfo = null;
    this.isLoggined = false;
    this.isAdmin = false;
    this.router.navigate(['/login']);
    this.snackBar.open("Вы вышли из учётной записи", "Успех", {
      duration: 2000
    });
    this.bucketService.clearBucket();
  }

}



