import {Injectable} from '@angular/core';
import {CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router} from '@angular/router';
import {Observable} from 'rxjs/Observable';
import {PdAuthService} from "./pd--auth.service";

@Injectable()
export class PdAuthGuard implements CanActivate {

  public constructor(private router: Router, private authService: PdAuthService) {
  }

  canActivate(next: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    if(this.authService.isLoggined){
      return true;
    }
    this.router.navigate(['/login']); //, { queryParams: { backUrl: state.url } }
    return false;
  }
}



