import { Pipe, PipeTransform } from '@angular/core';
import {StylesService} from "./pd--styles.service";

@Pipe({
  name: 'styles'
})
export class StylesPipe implements PipeTransform {

  constructor(private stylesService: StylesService){

  }

  transform(idStyle: any): any {
    return this.stylesService.styles.find(i => i.idStyle === idStyle).name;
  }

}



