import {Component, OnInit} from '@angular/core';
import {Http} from "@angular/http";
import {environment} from "../../environments/environment";
import {MatDialog, MatDialogConfig} from "@angular/material";
import {PdDialogComponent} from "../pd--dialog/pd--dialog.component";
import {StylesService} from "../pd--styles.service";

@Component({
  selector: 'app-pd--office-styles',
  templateUrl: './pd--office-styles.component.html',
  styleUrls: ['./pd--office-styles.component.scss']
})
export class PdOfficeStylesComponent implements OnInit {

  styles: any[];

  matDialogConfig: MatDialogConfig = new MatDialogConfig();

  constructor(public dialog: MatDialog, private styleService: StylesService) {
    this.styleService.getStyles().subscribe(res => {
      this.styles = res;
    });
  }

  ngOnInit() {
  }

  onUpdate(style) {
    let fields = [
      {name: 'Название', type: 'text', value: style.name, required: true},
      {name: 'Описание', type: 'textarea', value: style.description, required: false}
    ];
    this.matDialogConfig.data = {
      title: `Изменить стиль ${style.name}`,
      onSubmit: () => {
        this.styleService.updateStyle(style.idStyle, fields[0].value, fields[1].value).subscribe(res => this.styles = res);
      },
      onDelete: () => {
        this.styleService.deleteStyle(style.idStyle).subscribe(res => this.styles = res);
      },
      fields: fields
    };
    let dialogRef = this.dialog.open(PdDialogComponent, this.matDialogConfig);
    dialogRef.afterClosed().subscribe();
  }

  onAdd() {
    let fields = [
      {name: 'Название', type: 'text', value: null, required: true},
      {name: 'Описание', type: 'textarea', value: null, required: false}
    ];
    this.matDialogConfig.data = {
      title: `Добавить стиль`,
      onSubmit: () => {
        this.styleService.addStyle(fields[0].value, fields[1].value).subscribe(res => this.styles = res);
      },
      fields: fields
    };
    let dialogRef = this.dialog.open(PdDialogComponent, this.matDialogConfig);
    dialogRef.afterClosed().subscribe();
  }

}



