import { Injectable } from '@angular/core';
import {environment} from "../environments/environment";
import {Http} from "@angular/http";

@Injectable()
export class CollectionsService {

  private _base = environment["pd--api-url"];

  collections: any[] = [];

  constructor(private http: Http) { }

  getCollections(){
    return this.http.get(this._base + 'office/collections.php').map(res => this.collections = res.json());
  }

  addCollection(name: string, idStyle: string){
    return this.http.post(this._base + 'office/collections.php', JSON.stringify({name: name, idStyle: idStyle})).map(res => this.collections = res.json());
  }

  updateCollection(idCollection: number, name: string, idStyle: string){
    return this.http.put(this._base + 'office/collections.php', JSON.stringify({idCollection: idCollection, name: name, idStyle: idStyle})).map(res => this.collections = res.json());
  }

  deleteCollection(idCollection: number){
    return this.http.delete(this._base + 'office/collections.php/' + idCollection).map(res => this.collections = res.json());
  }

}



