import {Component, OnInit} from '@angular/core';
import {PdAuthService} from "../pd--auth.service";
import {Http} from "@angular/http";
import {environment} from "../../environments/environment";
import {Router} from "@angular/router";

import {MatSnackBar} from '@angular/material';

@Component({
  selector: 'app-pd--office-login',
  templateUrl: './pd--office-login.component.html',
  styleUrls: ['./pd--office-login.component.scss']
})
export class PdOfficeLoginComponent implements OnInit {

  constructor(private authService: PdAuthService) {
  }

  ngOnInit() {
  }

  onSubmit(form) {
    this.authService.logIn(form.value.login, form.value.password);
  }

}



