import { Component, OnInit } from '@angular/core';
import {Http} from "@angular/http";
import {environment} from "../../environments/environment";

@Component({
  selector: 'app-pd--styles',
  templateUrl: './pd--styles.component.html',
  styleUrls: ['./pd--styles.component.scss']
})
export class PdStylesComponent implements OnInit {

  private _base = environment["pd--api-url"];

  public styles: any[];

  constructor(private http: Http) {
    this.http.get(this._base + 'styles/').subscribe(res => this.styles = res.json());
  }

  ngOnInit() {
  }

}



