import { Component, OnInit } from '@angular/core';
import {Http} from "@angular/http";
import {environment} from "../../environments/environment";

@Component({
  selector: 'app-pd--remnants-report',
  templateUrl: './pd--remnants-report.component.html',
  styleUrls: ['./pd--remnants-report.component.scss']
})
export class PdRemnantsReportComponent implements OnInit {

  private _base = environment["pd--api-url"];

  products;

  date = new Date().toLocaleString("ru", {
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  });

  constructor(http: Http) {
    http.get(this._base + 'office/reports/remnants.php').map(res => this.products = res.json()).subscribe();
  }

  ngOnInit() {
  }

  print(){
    window.print();
  }

}



