import { Injectable } from '@angular/core';
import {Http} from "@angular/http";
import {environment} from "../environments/environment";

@Injectable()
export class PdProductsService {

  private _base = environment["pd--api-url"];

  products: any[] = [];

  constructor(private http: Http) { }

  getProducts(){
    return this.http.get(this._base + 'office/products.php').map(res => this.products = res.json());
  }

  addProduct(idType, idCollection, price, name, mark, length, width, height, dateOfCreation, description){
    return this.http.post(this._base + 'office/products.php', JSON.stringify({idType, idCollection, price, name, mark, length, width, height, dateOfCreation, description})).map(res => this.products = res.json());
  }

  updateProduct(idProduct, idType, idCollection, price, name, mark, length, width, height, description){
    return this.http.put(this._base + 'office/products.php', JSON.stringify({idProduct, idType, idCollection, price, name, mark, length, width, height, description})).map(res => this.products = res.json());
  }

  deleteProduct(idProduct: number){
    return this.http.delete(this._base + 'office/products.php/' + idProduct).map(res => this.products = res.json());
  }

}



