import { Component, OnInit, Input, AfterViewInit } from '@angular/core';

@Component({
  selector: 'image-scroller',
  templateUrl: './hk--image-scroller.component.html',
  styleUrls: ['./hk--image-scroller.component.css']
})
export class ImageScrollerComponent implements OnInit, AfterViewInit {

  fullScreenIn: string = "../../assets/fullscreen_in.svg";
  fullScreenExit: string = "../../assets/fullscreen_exit.svg";

  @Input() images: string[];
  private _currentImage: string;
      get currentImage(): string{
        return this._currentImage;
      }
      set currentImage(value: string){
        this._currentImage = value;
      }

  isFullScreen: boolean = false;

  isScrollVisible = true;

  isAnimate: boolean = false;

  private _isLeftSpaceEnded: boolean = true;
      get isLeftSpaceEnded(): boolean {
        return this._isLeftSpaceEnded;
      }

  private _isRightSpaceEnded: boolean = false;
      get isRightSpaceEnded(): boolean {
        return (this.images.length <= 3) || this._isRightSpaceEnded;
      }

  private _left: number = 0;
      get left(): number{
        return this._left;
      }
      set left(value: number){
        if(value >= -10){
          this._left = 0;
        }
        else if(value <= window.innerWidth - this.lentWidth + 10){
          this._left = window.innerWidth - this.lentWidth;
        }
        else{
          this._left = value;
        }

        this._isLeftSpaceEnded = this.left >= -10;
        this._isRightSpaceEnded = this.left <= window.innerWidth - this.lentWidth + 10;
      }

  private _imageWidth: number;
      get imageWidth(): number {
        if(this.images.length > 2)
          return this.displayWidth/3;
        return this.displayWidth/2;
      }

  private _lentWidth: number;
      get lentWidth(): number {
        return this.images.length * this.imageWidth;
      }

  private _displayWidth: number;
      get displayWidth(): number{
        return document.getElementsByClassName('hk--image-scroller')[0].clientWidth;
      }

  constructor() { }

  ngOnInit() {
    this.currentImage = this.images[0];

    if(this.images.length < 2) this.isScrollVisible = false;
  }

  ngAfterViewInit(){

    let scroller = document.getElementsByClassName('hk--image-scroller--view')[0];

    window.onresize = () => { this.left += 0;};

    if(!scroller || this.images.length < 4) return;

    let startPoint = new Point();
    let swipe = new Point();
    let startLeft;

    let startTime: any;

    scroller.addEventListener('touchstart', (event) => {
      startLeft = this.left;
      startPoint.x = event.changedTouches[0].pageX;
      startPoint.y = event.changedTouches[0].pageY;
      startTime = new Date();
    }, false);

    scroller.addEventListener('touchmove', (event) => {
      swipe.x = event.changedTouches[0].pageX - startPoint.x;
      swipe.y = event.changedTouches[0].pageY - startPoint.y;
      if(Math.abs(swipe.x) > Math.abs(swipe.y)) {
        event.preventDefault();
        this.left = startLeft + swipe.x;// * 2;
      }
    }, false);

    scroller.addEventListener('touchend', (event) => {

      if((event.target as HTMLElement).classList[0] === "hk--right-button--arrow" ||
         (event.target as HTMLElement).classList[0] === "hk--left-button--arrow") return;


      let endTime: any = new Date();
      let time = (endTime - startTime) / 1000;

      if(time > 0.5) return;

      this.isAnimate = true;
      console.log(time + "  " + swipe.x + "  " + swipe.x / (time));
      this.left = this.left + swipe.x / (time);
    }, false);

    scroller.addEventListener('transitionend', (event) => {
      if(event['propertyName'] == 'left'){
        this.isAnimate = false;
      }
    }, false);
  }

  chooseImage(i){
    this.currentImage = this.images[i];
  }

  clickLeft(event){
    this.left += this.imageWidth;
  }

  clickRight(event){
    this.left -= this.imageWidth;
  }

  clickFullScreen(){
    this.isFullScreen = !this.isFullScreen;
    document.body.style.overflow = this.isFullScreen ? "hidden" : "";
  }

  clickScrollViewOrHide(){
    this.isScrollVisible = !this.isScrollVisible;
  }
}

class Point{
  x: number;
  y: number;

  constructor() {
  }
}



