import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from "@angular/material";
import {FormControl} from "@angular/forms";
import {Observable} from "rxjs/Observable";
import 'rxjs/add/operator/startWith';
import 'rxjs/add/operator/map';

@Component({
  selector: 'app-pd--dialog',
  templateUrl: './pd--dialog.component.html',
  styleUrls: ['./pd--dialog.component.scss']
})
export class PdDialogComponent implements OnInit {


  myControl: FormControl = new FormControl();

  options = [
    'One',
    'Two',
    'Three'
  ];

  filteredOptions: Observable<string[]>;

  ngOnInit() {
    this.filteredOptions = this.myControl.valueChanges
      .startWith(null)
      .map(val => val ? this.filter(val) : this.options.slice());
  }

  filter(val: string): string[] {
    console.log(val);
    return this.options.filter(option =>
      option.toLowerCase().indexOf(val.toLowerCase()) === 0);
  }



  isConfirm: boolean = false;

  constructor(public dialogRef: MatDialogRef<PdDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: any) {}

  onCancel(){
    this.dialogRef.close();
  }

  onDelete(){
    this.isConfirm = true;
  }

  onSubmit(){
    this.data.onSubmit && this.data.onSubmit.call(this);
    this.dialogRef.close();
  }

  onConfirm(){
    this.data.onDelete && this.data.onDelete();
    this.dialogRef.close();
  }

  onCancelConfirm(){
    this.isConfirm = false;
  }

}

class DialogData{

}



