import { Component, OnInit } from '@angular/core';
import {Http} from "@angular/http";
import {environment} from "../../environments/environment";

@Component({
  selector: 'app-pd--collections-page',
  templateUrl: './pd--collections-page.component.html',
  styleUrls: ['./pd--collections-page.component.scss']
})
export class PdCollectionsPageComponent implements OnInit {

  private _base = environment["pd--api-url"];

  styles: any[];
  isReady: boolean = false;

  constructor(private http: Http) {
    this.http.get(this._base + "collections/index.php").subscribe((res) => {
      this.styles = res.json();
      this.isReady = true;
    });
  }

  ngOnInit() {
  }

}



